/* global window */

import { AxiosRequestConfig } from "axios";

// store.ts
interface StoreState {
  pendingResolvers: { resolve: (_config: AxiosRequestConfig) => void; config: AxiosRequestConfig }[];
  concurrentCount: number;
  recevied: number;
  processed: number;
}

const MAX_REQUESTS_COUNT = 5;

class RequestStore {
  private state: StoreState;
  private listeners: Set<() => void>;

  constructor() {
    this.state = { pendingResolvers: [], concurrentCount: 0, recevied: 0, processed: 0 };
    this.listeners = new Set();
  }

  getState(): StoreState {
    return this.state;
  }

  handleRequest(resolve: (_config: AxiosRequestConfig) => void, config: AxiosRequestConfig): void {
    if (this.state.concurrentCount >= MAX_REQUESTS_COUNT) {
      this.state.pendingResolvers.push({ resolve, config });
    } else {
      this.state.concurrentCount++;
      if (config["controller"]) {
        // this.state.runningAbortControllers.push(config["controller"]);
      }
      resolve(config);
    }
    this.state.recevied++;

    this.notify();
  }

  handleRequestDone(): void {
    if (this.state.pendingResolvers.length > 0) {
      const { resolve, config } = this.state.pendingResolvers.shift();
      resolve(config);
      //   if (config["controller"]) {
      //     this.state.runningAbortControllers.push(config["controller"]);
      //   }
    } else {
      //   this.state.concurrentCount = Math.max(0, this.state.concurrentCount - 1);
      //   this.state.runningAbortControllers = this.state.runningAbortControllers.filter((s) => !s.signal.aborted);
    }
    this.state.processed++;
    this.notify();
  }

  abortAllRequests(): void {
    this.state.pendingResolvers = this.state.pendingResolvers.filter(({ config }) => {
      if (config["controller"]) {
        config["controller"].abort();
        return false;
      }
      return true;
    });
    // this.state.runningAbortControllers.filter((s) => !s.signal.aborted).forEach((s) => s.abort());
    // this.state.runningAbortControllers = [];
    this.notify();
  }

  setState(newState: StoreState | ((_prev?: StoreState) => StoreState)): void {
    if (typeof newState === "function") {
      const state = newState(this.state);
      this.state = state;
    } else {
      this.state = newState;
    }
    this.notify();
  }

  subscribe(listener: () => void): () => void {
    this.listeners.add(listener);
    return () => this.listeners.delete(listener);
  }

  private notify(): void {
    console.log(
      this.state.pendingResolvers.length,
      this.state.concurrentCount,
      this.state.recevied,
      this.state.processed
    );
    this.listeners.forEach((listener) => listener());
  }
}
if (!window["store"]) {
  window["store"] = new RequestStore();
}
export default window["store"];
